import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TaskServiceService } from './../task-service.service';
import { Tasks } from './../tasks';

@Component({
  selector: 'app-task-details-component',
  templateUrl: './task-details-component.component.html',
  styleUrls: ['./task-details-component.component.css']
})
export class TaskDetailsComponentComponent implements OnInit {


  constructor(private route: ActivatedRoute,
              private TaskService: TaskServiceService,
              private router: Router) { }

  task: Tasks;
  viewPage = true;
  updatePage = false;
  /**
   */
  ngOnInit() {
    let id = 0;
    this.route.params.subscribe(params => {
      id = params['id'];
    });
    this.getTask(id);
  }

  getTask(id) {
    this.TaskService.getTask(id).subscribe(
      data => { this.task = data; console.log(this.task); }
    );
  }

  /**
   */
  changeToUpdate() {
    this.viewPage = false;
    this.updatePage = true;
  }
  /**
   */
  changeToView() {
    this.viewPage = true;
    this.updatePage = false;
  }

  updateTask(newDescription: string, newIsComplete: boolean) {
    this.task.description = newDescription;
    this.task.isComplete = newIsComplete;
    this.TaskService.updateTask(this.task);
    let id = 0;
    this.route.params.subscribe(params => {
      id = params['id'];
    });
    this.getTask(id);
    this.changeToView();
  }
}
