import { Component, OnInit, Input } from '@angular/core';
import { TaskServiceService } from './../task-service.service';
import { Tasks } from './../tasks';
import { Router } from '@angular/router';
import { TaskDetailsComponentComponent } from '../task-details-component/task-details-component.component';

@Component({
  selector: 'app-task-list-item-component',
  templateUrl: './task-list-item-component.component.html',
  styleUrls: ['./task-list-item-component.component.css']
})
export class TaskListItemComponentComponent implements OnInit {

  constructor(private TaskService: TaskServiceService,
              private router: Router) { }

  @Input() taskId: number;
  task;
  ngOnInit() {
    this.getTask(this.taskId);
  }

  getTask(id) {
    this.TaskService.getTask(id).subscribe(
      data => { this.task = data; }
    );
  }
  /**
   */
  deleteTask() {
    this.TaskService.deleteTask(this.task._id);
    document.getElementById('taskBox').style.display = 'none';
  }
  /**
   */
  updateTask() {
    this.router.navigate(['/tasks/' + this.task._id]);
    TaskDetailsComponentComponent.prototype.changeToUpdate();
  }

}
