import { Injectable, Input } from '@angular/core';
import { Tasks } from './tasks';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class TaskServiceService {


  constructor(private http: HttpClient) { }

  APIURL = 'http://cosc484a7back.herokuapp.com/api/tasks';
    /*
   * @param  {} descriptionp
   */
  createTask(descriptionp: string) {

    const httpHeaders = new HttpHeaders({'Content-type': 'application/x-www-form-urlencoded' });
    const options = { headers: httpHeaders };

    const body = new HttpParams()
      .set('description', descriptionp);

    this.http.post(this.APIURL,  body.toString(), options)
      .subscribe( response => console.log(response), err => console.log(err));


  }
  /*
   * @param  {number} id
   */

  getTask(id: number) {
    return this.http.get<Tasks>(this.APIURL + '/' + id);
  }
  /*
   * @param  {Tasks} task
   */
  updateTask(task: Tasks) {
    const patchHttpHeaders = new HttpHeaders({'Content-type': 'application/x-www-form-urlencoded' });
    const patchOptions = { headers: patchHttpHeaders };

    const body = new HttpParams()
      .set('description', task.description)
      .set('isComplete', task.isComplete.toString());

    this.http.patch(this.APIURL + '/' + task._id, body.toString(), patchOptions)
      .subscribe( response => console.log(response), err => console.log(err));
  }
  /*
   * @param  {number} id
   */
  deleteTask(id: number) {
    const httpHeaders = new HttpHeaders({'Content-type': 'application/x-www-form-urlencoded' });
    const options = { headers: httpHeaders };

    const body = new HttpParams()
      .set('id', id.toString());

    this.http.post(this.APIURL + '/' + id, body.toString(), options)
      .subscribe( response => console.log(response), err => console.log(err));
  }

  getAllTasks() {
    return this.http.get(this.APIURL);
  }

  clearStorage() {
    localStorage.clear();
  }
}
